<template>
  <div class="goodsDetail">
    <transition name="fade">
      <div v-show="!fade" class="header">
        <van-icon name="arrow-left" @click="$toPage(-1)" />
        <router-link
          :to="{ name: 'detail' }"
          replace
          @click.native="switchClass(1)"
          class="linkA"
          >商品详情</router-link
        >
        <!-- <router-link
          :to="{ name: 'comment' }"
          replace
          @click.native="switchClass(2)"
          :class="{ linkA: flag, linkB: !flag }"
          >评论</router-link
        > -->
        <van-icon name="label-o" @click="toCart" />
      </div>
    </transition>
    <transition name="fade">
      <div class="headIcon" v-show="fade">
        <img
          src="..\..\assets\goodsDetails_icon\back.png"
          @click="$toPage(-1)"
        />
        <img
          src="..\..\assets\goodsDetails_icon\caigoudan.png"
          @click="toCart"
        />
      </div>
    </transition>
    <div class="main">
      <keep-alive>
        <transition name="slide-fade">
          <router-view v-if="isShowPage" @activeInfo="activeInfo"></router-view>
        </transition>
      </keep-alive>
    </div>

    <van-goods-action v-show="flag">
      <van-goods-action-mini-btn
        icon="service-o"
        text="客服"
        @click="$openTel(serviceQQ)"
      />
      <van-goods-action-mini-btn
        @click="isCollect"
        :icon-class="collectClass"
        icon="star-o"
        text="收藏"
      />
      <van-goods-action-mini-btn icon="share" text="分享" @click="share" />
      <van-goods-action-big-btn
        @click="SaveShopCart(0)"
        type="warning"
        text="加入购物车"
      />
      <van-goods-action-big-btn
        type="danger"
        text="立即购买"
        @click="SaveShopCart(1)"
      />
    </van-goods-action>
    <van-sku
      v-model="showBase"
      :sku="sku"
      :goods="goodsInfo"
      :quota="quota"
      reset-stepper-on-hide
      reset-selected-sku-on-hide
      close-on-click-overlay
      :show-add-cart-btn="false"
      buy-text="确定"
      @buy-clicked="onBuyClicked"
    >
      <div class="template" slot="sku-stepper">
        <div class="number">
          <p>购买数量：</p>
          <p>
            <span>剩余{{ goods.store }}件</span>
            <span>{{ goods.medpacknum }}件起购</span>
          </p>
        </div>
        <div class="stepper">
          <van-stepper
            v-model="count"
            integer
            :step="goods.medpacknum"
            :max="goods.appLimitnum"
            :min="goods.medpacknum"
            @focus="focusDialog"
            @overlimit="overlimit(count)"
          />
        </div>
      </div>
    </van-sku>
    <van-dialog v-model="dialog" :showCancelButton="false" title="输入数量" show-cancel-button>
      <van-field v-model="count" type="number" input-align="center" placeholder="请输入数量" ref="input" @blur="inputCount" />
    </van-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isShowPage: false,
      flag: true,
      goods: {},
      showBase: false,
      count: 0,
      sku: {
        tree: [],
        list: [],
        price: 0,
        hideStock: true,
        stock_num: 0
      },
      quota: 0,
      goodsInfo: {
        title: "",
        picture: ""
      },
      state: true,
      collectClass: "",
      discounts: [],
      unqid2: 0,
      goodsDetailSku: 0,
      buyNum: 0,
      fade: false,
      scrollTop: 0,
      serviceQQ: null,
      activeInfoS: {},
      dialog: false
    };
  },
  computed: {
    userInfo() {
      return this.$loadUserInfo();
    },
  },
  mounted() {
    //  获取商品sku
    this.goodsDetailSku = this.$route.query.sku
      ? this.$route.query.sku
      : sessionStorage.getItem("goodsDetailSku");
    //  清除缓存
    // sessionStorage.removeItem('goodsDetailSku')
    this.getProd();
    window.addEventListener("scroll", this.handleScroll, true);
    this.getServiceInfo();
  },
  methods: {
    //  秒杀价格
    activeInfo(data) {
      this.activeInfoS = data;
    },
    handleScroll() {
      this.scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (this.scrollTop > 50) {
        this.fade = true;
      } else {
        this.fade = false;
      }
    },
    //  跳采购单
    toCart() {
      sessionStorage.setItem("goodsDetailSku", this.goods.sku);
      this.$router.replace({
        path: "/shoppingCart",
        query: {
          cart: true
        }
      });
    },
    //  限制
    overlimit(i) {
      if (i >= this.goods.limits) {
        this.$toast("超过购买限制了哟！");
      } else {
        this.$toast("受不了了，商品不能再减少了哦！");
      }
    },
    focusDialog() {
      this.dialog = true
      this.$nextTick(() => {
        this.$refs.input.focus()
      })
    },
    inputCount() {
      this.count = Math.ceil(this.count / this.goods.medpacknum) * this.goods.medpacknum
      if(this.count <= 0){
        this.count = this.goods.medpacknum
      }else if(this.count > this.goods.appLimitnum){
        this.count = this.goods.appLimitnum > 0 ? this.goods.appLimitnum : this.goods.medpacknum
      }
    },
    onClickback() {
      this.$router.go("-1");
      delete this.$router["params"];
    },
    //  更换样式
    switchClass(i) {
      if (i === 1) {
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
    //  获取商品信息
    getProd() {
      JNB.query({
        serverName: "goodsServer",
        cls: "BackgroundProdModule",
        method: "getProd",
        params: [this.goodsDetailSku],
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            this.goods = result.data;
            this.getActive();
            this.isCollectA();
            if (this.goods.appLimitnum === 0) {
              this.goods.appLimitnum = this.goods.store;
            }
            this.count = result.data.medpacknum;
            this.isShowPage = true;
            //获取商品图片
            this.goods.arr = [
              {
                sku: this.goods.sku,
                spu: this.goods.spu
              }
            ];
            this.$request_img({
              context: this,
              arr: this.goods.arr,
              imageType: "600x600"
            });
            this.$router.params = { goods: this.goods };
            //  添加足迹
            this.addFooter();
          }
        }
      });
    },
    //  添加足迹
    addFooter() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "MyFootprintModule",
        method: "add",
        params: {
          sku: this.goods.sku
        },
        callback: data => {}
      });
    },
    //  活动商品信息
    getActive() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "CalculateModule",
        method: "getActivitiesBySKU",
        params: [this.goods.sku, this.userInfo.comp.storeId],
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            if (result.data.discounts.length > 0) {
              this.discounts = result.data.discounts;
              let arrlimit = [];
              // result.data.discounts.forEach(data => {
              //   if (data.limitnum !== 0) {
              //     arrlimit.push(data.limitnum);
              //   }
              // });
              if (result.data.minimum < this.goods.appLimitnum) {
                this.goods.appLimitnum = result.data.minimum;
              }
            }
            if (
              result.data.discounts.length > 0 &&
              result.data.discounts[0].brulecode == 1113
            ) {
              //活动码
              this.beforeSecKill();
              this.state = false;
            }
          }
        }
      });
    },
    //  点击 判断是否收藏
    isCollect() {
      if (this.collectClass == "") {
        JNB.query({
          serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
          cls: "MyCollectModule",
          method: "check",
          params: {
            compid: this.userInfo.comp.storeId,
            sku: this.goods.sku
          },
          callback: data => {
            let result = JSON.parse(data);
            if (result.code === 200) {
              if (result.data) {
                this.$toast("该商品已经收藏！");
              } else {
                //  调用收藏方法
                this.AddCollection();
              }
            }
          }
        });
      } else {
        this.delCollect();
      }
    },
    //  进入页面自动调用 判断是否收藏
    isCollectA() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "MyCollectModule",
        method: "check",
        params: {
          compid: this.userInfo.comp.storeId,
          sku: this.goods.sku
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200 && result.data) {
            this.collectClass = "iconClass";
          }
        }
      });
    },
    //  取消收藏
    delCollect() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "MyCollectModule",
        method: "del",
        params: {
          unqid: this.goods.unqid,
          sku: this.goods.sku
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            this.$toast("取消收藏");
            this.collectClass = "";
          }
        }
      });
    },
    //  添加收藏
    AddCollection() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "MyCollectModule",
        method: "add",
        params: {
          sku: this.goods.sku,
          promtype: 0,
          prize: this.vatp
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            this.collectClass = "iconClass";
            this.$toast(result.data);
          }
        }
      });
    },
    //  点击购物车按钮
    SaveShopCart(index) {
      this.buyNum = index;
      this.goodsInfo.title = this.goods.prodname + " " + this.goods.spec;
      this.sku.stock_num = this.goods.store;
      this.sku.price =
        this.goods.vatp == -1
          ? "未认证"
          : this.goods.vatp == -2
          ? "未签约"
          : this.activeInfoS.killPrice
          ? this.activeInfoS.killPrice
          : this.goods.vatp;
      this.showBase = !this.showBase;
      this.goodsInfo.picture = this.goods.arr[0].imgURl;
      //  获取购物车数量
      this.getGoodsCount();
    },
    //  加入购物车
    onAddCartClicked() {
      if (this.goods.vatp !== -1) {
        this.showBase = false;
        JNB.query({
          serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
          cls: "AppModule",
          method: "addCartOption",
          params: {
            sku: this.goods.sku,
            number: this.count
          },
          callback: data => {
            let result = JSON.parse(data);
            if (result.code == 200) {
              this.$toast(result.message);
            } else {
              this.$toast.fail(result.message);
            }
          }
        });
      } else {
        this.$toast("请认证后再购买~");
      }
    },
    //  获取购物车数量
    getGoodsCount() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "ShoppingCartModule",
        method: "remoteQueryShopCartNumBySku",
        params: [this.userInfo.comp.storeId, this.goods.sku + ""],
        callback: data => {
          let result = JSON.parse(data);
          if (result > 0) {
            this.count = result;
          }
        }
      });
    },
    //  获取秒杀商品信息
    beforeSecKill() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "SecKillModule",
        method: "beforeSecKill",
        params: {
          sku: this.goods.sku,
          actno: this.discounts[0].unqid
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            this.unqid2 = result.data;
          }
        }
      });
    },
    //  立即购买
    onBuyClicked() {
      if (this.buyNum == 0) {
        this.onAddCartClicked();
      } else {
        if (this.goods.vatp !== -1) {
          JNB.query({
            serverName:
              "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
            cls: "ShoppingCartModule",
            method: "querySettShopCartList",
            params: [
              {
                pdno: this.goods.sku,
                pnum: this.count,
                compid: this.userInfo.comp.storeId,
                checked: 1,
                unqid: 0,
                conpno: 0,
                areano: this.userInfo.comp.addressCode,
                pdprice: this.goods.vatp
              }
            ],
            callback: data => {
              let result = JSON.parse(data);
              if (result.code == 200) {
                result.data.goods[0].imgURl = this.goods.arr[0].imgURl;
                if (
                  result.data.goods[0].status !== 2 &&
                  result.data.goods[0].status !== 3
                ) {
                  sessionStorage.setItem("goodsDetailSku", this.goods.sku);
                  this.$router.push({
                    path: "/goodsOrder",
                    query: {
                      placeType: 1
                    }
                  });
                  sessionStorage.setItem(
                    "placeOrderList",
                    JSON.stringify(result.data.goods)
                  );
                  sessionStorage.setItem(
                    "activeOrderList",
                    JSON.stringify(result.data.gifts)
                  );
                } else {
                  if (result.data.goods[0].status === 2) {
                    this.$toast("商品已下架，不可购买！");
                  } else {
                    this.$toast("商品库存不足！");
                  }
                }
              } else {
                this.$toast.fail(result.message);
              }
            }
          });
        } else {
          this.$toast("请认证后再购买~");
        }
      }
    },
    //  分享
    share() {
      this.$toast("糟糕，没有可分享的朋友~");
    },
    //获取客服信息
    getServiceInfo() {
      JNB.query({
        serverName: "globalServer",
        cls: "WebAppModule",
        method: "customerInfo",
        callback: data => {
          data = JSON.parse(data);
          if (data.code == 200) {
            data.data = JSON.parse(data.data);
            this.tel = data.data.compTel;
            this.serviceQQ =
              data.data.customList[
                Math.floor(Math.random()) * data.data.customList.length
              ].qq;
          }
        }
      });
    }
  }
};
</script>
<style scoped>
.goodsDetail {
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
}
.goodsDetail .header {
  width: 100%;
  height: 92px;
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  position: fixed;
  top: 0;
  background-color: #fff;
  z-index: 99;
}
.goodsDetail .headIcon {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
}
.goodsDetail .headIcon img {
  width: 58px;
  height: 58px;
  background: rgba(204, 204, 204, 1);
  border-radius: 50%;
  margin: 20px;
  opacity: 0.8;
}
.goodsDetail .headIcon img:last-child {
  float: right;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.8s;
}
.goodsDetail .header .van-icon {
  margin: 0 20px;
}
.goodsDetail .header .van-icon,
.goodsDetail .header a {
  font-size: 46px;
  height: 100%;
  line-height: 92px;
}
.goodsDetail .header .linkA {
  font-size: 32px;
  text-decoration: none;
  color: rgba(51, 51, 51, 1);
}
.goodsDetail .header .linkB {
  font-size: 32px;
  text-decoration: none;
  color: rgba(51, 51, 51, 1);
  box-sizing: border-box;
  border-bottom: 4px solid rgba(237, 47, 38, 1);
}
.goodsDetail .main {
  width: 100%;
  padding-top: 80px;
}
.goodsDetail .template {
  overflow: hidden;
}
.goodsDetail .template .stepper {
  float: right;
  margin-bottom: 20px;
}
.goodsDetail .template input {
  width: 60px;
  height: 48px;
  text-align: center;
  margin: 0 5px;
  border: none;
  border-radius: 3px;
  background-color: #f2f3f5;
}
.goodsDetail .template button {
  width: 50px;
  height: 50px;
  border-radius: 2px;
  border: none;
  background-color: #f2f3f5;
}
.goodsDetail .template .number {
  float: left;
}
.goodsDetail .template .number p:first-child {
  margin: 0;
  padding: 0;
  padding: 10px 20px 5px;
  font-size: 28px;
}
.goodsDetail .template .number p:last-child {
  margin: 0;
  padding: 0;
  padding: 10px 20px 20px;
  font-size: 20px;
  color: rgba(153, 153, 153, 1);
}
.goodsDetail .template .number p:last-child span:last-child {
  margin-left: 20px;
  color: red;
}
/* 去除input自带加减 */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.slide-fade {
  position: absolute;
  left: 0;
  right: 0;
}
.slide-fade-enter-active,
.slide-fade-leave-active {
  transition: opacity 1s;
}
.slide-fade-enter,
.slide-fade-leave-to {
  opacity: 0;
}
.goodsDetail .iconClass {
  color: red;
}
</style>

